.flash-message__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 5vh;
  position: fixed;
  top: 0vh;
  animation: message 2s ease-in-out;
  z-index: 1;

  &.flash-message__error {
    background-color: #be0000;
  }
  &.flash-message__info {
    background-color: #9e9d89;
  }
  &.flash-message__success {
    background-color: $buttonColor;
  }
  span {
    color: $white;
    font-size: 150%;
  }
}

@keyframes message {
  0% {
    transform: translateY(0px);
  }
  5% {
    transform: translateY(5vh);
  }
  95% {
    transform: translateY(5vh);
  }
  100% {
    transform: translateY(0px);
  }
}
